/**
 * UNAS Cart Service  kosrhoz ads s kosr lekrdezs
 * A widget UNAS oldalon fut, gy window.cart_add s UNAS.getCart elrhet.
 *
 * cart_add(productId, prefix, null, quantity)  termk oldalon 4 paramter
 * A cart_add a mennyisg inputot keresi: id="db_{prefix}{productId}" vagy id="db_{productId}"
 * UNAS.getCart(callback, { lang: 'hu' })  kosr tartalom
 */

const CART_ADD_PREFIX = '';

/**
 * UNAS termk ID formtum: VX__unas__{id}
 * Preferlja: unas_id, unas_ref, id, sku. Kiszri az rvnytelen ID-kat (pl. unas-prod-*).
 */
function toUnasProductId(product) {
  const id = product?.unas_id ?? product?.unas_ref ?? product?.id ?? product?.sku;
  if (!id) return null;
  const str = String(id).trim();
  // rvnytelen ID-k (pl. CSV parser unas-prod-1): ne adjunk VX__unas__ prefixet
  if (str.startsWith('unas-prod-') || str === '') return null;
  if (str.startsWith('VX__unas__')) return str;
  return `VX__unas__${str}`;
}

/**
 * cart_add s a hozz tartoz window  lehet iframe-ben, parent vagy top window-ban.
 * Fontos: az inputot UGYANABBA a documentba kell tenni, ahol a cart_add fut (pl. parent),
 * mert a cart_add document.getElementById()-ot hasznl a sajt documentjn.
 */
function getCartAddContext() {
  if (typeof window === 'undefined') return null;
  const wins = [window, window.parent, window.top].filter(Boolean);
  for (const w of wins) {
    try {
      if (typeof w.cart_add === 'function') return { cartAdd: w.cart_add, win: w };
    } catch (_) { /* cross-origin */ }
  }
  return null;
}

function getCartAdd() {
  const ctx = getCartAddContext();
  return ctx ? ctx.cartAdd : null;
}

/**
 * UNAS form keresse  kosr/termk form, ahova az input kerlhet
 */
function findUnasForm(doc) {
  if (!doc) return null;
  const selectors = [
    'form[action*="cart"]',
    'form[id*="cart"]',
    'form[name*="cart"]',
    'form[action*="kosar"]',
    'form#product_form',
    'form.product-form'
  ];
  for (const sel of selectors) {
    try {
      const form = doc.querySelector(sel);
      if (form) return form;
    } catch (_) { /* invalid selector */ }
  }
  return null;
}

/**
 * Mennyisg input ltrehozsa  a cart_add UGYANABBAN a documentban keresi!
 * Ha iframe-ben futunk, a cart_add a parent/top-ban van  az inputot is oda kell tenni.
 */
function ensureQtyInput(unasId, qty, targetDoc) {
  if (!targetDoc) return null;
  const inputId = `db_${unasId}`;
  let input = targetDoc.getElementById(inputId);
  if (!input) {
    input = targetDoc.createElement('input');
    input.type = 'number';
    input.name = 'db';
    input.id = inputId;
    input.value = String(qty);
    input.setAttribute('data-min', '1');
    input.setAttribute('data-max', '999999');
    input.style.cssText = 'position:absolute;left:-9999px;width:1px;height:1px;opacity:0;pointer-events:none;';
    const form = findUnasForm(targetDoc);
    (form || targetDoc.body).appendChild(input);
  } else {
    input.value = String(qty);
  }
  return input;
}

/**
 * Kosrhoz ads az UNAS kosrba (ha window.cart_add elrhet).
 * @param {object} product - termk objektum (id, unas_id)
 * @param {number} quantity - mennyisg
 * @returns {boolean} - true ha sikeres, false ha nem elrhet vagy hiba
 */
export function addToUnasCart(product, quantity = 1) {
  if (typeof window === 'undefined') return false;
  const ctx = getCartAddContext();
  if (!ctx || typeof ctx.cartAdd !== 'function') return false;

  const unasId = toUnasProductId(product);
  if (!unasId) return false;

  const qty = Math.max(1, Math.floor(Number(quantity) || 1));

  try {
    // Mennyisg input  a cart_add UGYANABBAN a documentban keresi (pl. parent/top)
    let targetDoc;
    try {
      targetDoc = ctx.win.document;
    } catch (_) {
      targetDoc = typeof document !== 'undefined' ? document : null;
    }
    if (!targetDoc) return false;
    ensureQtyInput(unasId, qty, targetDoc);
    // 4 paramter: productId, prefix ('' = termk oldal), null, quantity
    ctx.cartAdd(unasId, CART_ADD_PREFIX, null, qty);
    if (window.__MARKETLY_DEBUG) {
      console.log('[unasCartService] cart_add called:', { unasId, qty }, ` cart_add('${unasId}','',null,${qty})`);
    }
    return true;
  } catch (err) {
    if (window.__MARKETLY_DEBUG || process.env.NODE_ENV === 'development') {
      console.warn('[unasCartService] cart_add error:', err);
    }
    return false;
  }
}

/**
 * Ellenrzi, hogy az UNAS kosr API elrhet-e.
 */
export function isUnasCartAvailable() {
  return typeof getCartAdd() === 'function';
}

/**
 * UNAS kontextus  getCart lehet iframe-ben, parent vagy top window-ban.
 */
function getUnasContext() {
  if (typeof window === 'undefined') return null;
  const wins = [window, window.parent, window.top].filter(Boolean);
  for (const w of wins) {
    try {
      if (w.UNAS && typeof w.UNAS.getCart === 'function') return w.UNAS;
    } catch (_) { /* cross-origin */ }
  }
  return null;
}

/**
 * Kosr tartalom lekrdezse (UNAS.getCart).
 * @param {function} callback - (result) => void, result: { sum, items }
 * @param {object} opts - { lang: 'hu' }
 */
export function getUnasCart(callback, opts = {}) {
  if (typeof window === 'undefined') return;
  const UNAS = getUnasContext();
  if (!UNAS || typeof UNAS.getCart !== 'function') {
    if (callback) callback({ sum: 0, items: [] });
    return;
  }
  try {
    UNAS.getCart(callback, { lang: opts.lang || 'hu' });
  } catch (err) {
    if (process.env.NODE_ENV === 'development') {
      console.warn('[unasCartService] getCart error:', err);
    }
    if (callback) callback({ sum: 0, items: [] });
  }
}

/**
 * UNAS getCart vlasz talaktsa FloatingCartPreview formtumra.
 * @param {object} result - { sum, items } UNAS.getCart callback param
 * @returns {Array} cartItems: { id, name, price, quantity, images }
 */
export function transformUnasCartToItems(result) {
  const items = result?.items;
  if (!Array.isArray(items)) return [];
  return items
    .filter((item) => item.id != null || item.sku)
    .map((item, idx) => ({
      id: String(item.id ?? item.sku ?? `item-${idx}`),
      name: item.name || 'Termk',
      price: item.prices?.price_gross ?? item.prices?.price ?? 0,
      quantity: Number(item.qty) || 1,
      images: []
    }));
}
